.hero{
    display: flex;
    justify-content: space-between;
    background-color:#363d42;
}

.left-h{
    padding: 2rem;
    padding-top: 1.5rem;
  flex:3;
  display:flex;
  gap:2rem;
  flex-direction: column;
}

.right-h{
    flex:1;
    position: relative;
    background-color: orange;
}

.the-add{
    margin-top: 4rem;
    background-color: #363d42;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    text-transform: uppercase;
    color: white;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.the-add>div{
    position: absolute;
    background-color:orange ;
    width: 5.4rem;
    height: 80%;
    left:8px;
    border-radius: 3rem;
    z-index: 1;
}

.the-add>span{
    z-index: 2;
}

.hero-text{
    display: flex;
    flex-direction: column;
    gap:1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: white;
    text-overflow: inherit;
}

.hero-text>div:nth-of-type(3){
    font-size: 1rem;
    /* font-weight: 200; */
    text-transform: none;
    letter-spacing: 1px;
    width: 80%;
  
}

  span.works {
      margin-right: 17rem;
  }

.hero-text>div:nth-of-type(2) {
    letter-spacing: 1px;
    width: 50%;

}

.hero-text>div:nth-of-type(1) {
    letter-spacing: 1px;
    width: 50%;
}
.figures{
    display: flex;
    gap:2rem;
}

.figures>div{
    display: flex;
    flex-direction: column;
}

.figures>div>span:nth-of-type(1){
    color: white;
    font-size: 2rem;
 
}
.figures>div>span:nth-of-type(2) {
    color: gray;
        text-transform: uppercase;
}

.learnmore{
    display: flex;
    gap:1rem;
    font-weight: normal;

}

.learnmore>:nth-child(1){
    color:white;
    background-color: orange;
    width: 8rem;
    border-radius: 20px;
}
.learnmore>:nth-child(2) {
    color: white;
    background-color: transparent;
    width: 8rem;
    border: 2px solid orange;
    border-radius: 20px;
}


.learnmore>:nth-child(2):hover {
    color: orange;

}

.right-h> .btn{
    position:  absolute;
    right: 3rem;top:2rem;color: black;
    border-radius: 20px;
}

.heart-rate{
    display: flex;
    flex-direction: column;
    gap:1rem;
    background-color: #363d42;
    width: fit-content;
    padding: 1rem;
    align-items: start;
    border-radius: 20px;
    position: absolute;
    right: 4rem;
    top:7rem;
}

.heart-rate>:nth-child(2){
    color: white;

}


.heart-rate>:nth-child(3) {
    color: white;
    font-size: 1.5rem;

}

.heart-rate>img{
    width: 2rem;
}

.hero-img{
    position: absolute;
    top:7rem;
    right: 8rem;
    width: 30rem;
}

