.hero-blur{
    width: 22rem;
    height: 30rem;
    left: 0;
}

@media screen  and (max-width:768px){
    .hero{
        flex-direction: column;
    }
    .hero-blur{
        width:14rem;

    }

    .the-best-ad{
        margin-top: 0;
        font-size: small;
        align-self: center;
        transform: scale(0.8);
    }
    .hero-text{
        font-size: xx-large;
        align-items: center;
        justify-content: center;
    }

    .hero-text>div:nth-of-type(3){
        font-size: small;
        font-weight: 200px;
        letter-spacing: 1px;
        text-align: center;
    }

    .learnmore{
        justify-content: center;
    }

    .figures>div>span:nth-of-type(1){
        font-size: large;

    }

         .figures>div>span:nth-of-type(2) {
             font-size: small;
    
         }

         .right-h{
            position: relative;
            background: none;

         }

         .heart-rate{
            left:1rem;
            top:2rem;
         }

         .hero-img{
            position: relative;
            width: 15rem;
            left: 7rem;
            top: 4rem;
            align-self: center;
         }

}

